.nav{
    position: fixed;
    padding:20px;
    top: 0;
    display: flex;
    width: 100%;
    justify-content: space-between;
    z-index: 10;
    transition-timing-function: ease-in-out;
    transition: all 0.5s;
}

.nav__black{
    background-color: #000;
}

.nav__logo{
    width: 80px;
    object-fit: contain;
}

